@font-face {
  font-family: 'medium';
  src: url('/src/components/fonts/SFPRODISPLAYMEDIUM.ttf');
}

@font-face {
  font-family: 'regular';
  src: url('/src/components/fonts/SFPRODISPLAYREGULAR.ttf');
}

.portfolio-page-wrapper {
  margin:0;
  min-height: 100vh;
  min-width: 100vw;
  background: #1f1f1f;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.header {
  text-align: center;
  width: 100%;
  height: 100%;
  padding:20px;
}

.text {
  margin:0;
  font-family: 'medium';
  font-size:3vw;
  background: 
    radial-gradient(circle at var(--x) var(--y), rgba(255, 255, 255, 255), transparent 20%),
    #808080;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}

/* Hover mechanic */
.largeapp, .mediumapp, .div, .mediumapp-2, .mediumapp-3, .largeapp-2, .smallapp, .smallapp-2, .smallapp-3, .smallapp-4 {
  transition: transform 0.3s ease-in-out;
}
.largeapp:hover, .mediumapp:hover, .div:hover, .mediumapp-2:hover, .mediumapp-3:hover, .largeapp-2:hover, .smallapp:hover, .smallapp-2:hover, .smallapp-3:hover, .smallapp-4:hover {
  transform: scale(0.95);
}

@media (max-width: 1270px) {
  .frame-wrapper {
    width: 603px;
    height: 1335px;
  }
}

.app-header {
  font-family: 'regular', sans-serif;
  color: #000000; 
  font-size: 24px; 
  margin-left: 0.8em;
}

.app-body {
  font-family: 'regular', sans-serif;
  color: #000000;
  font-size: 18px;
  margin-left: 0.8em;
  margin-right:0.8em;
}

.box {
  background-color: transparent;
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

.box .frame-wrapper {
  border: 0px none;
  height: 730px;
  position: relative;
  width: 1205px;
}

.box .frame {
  height: 730px;
  left: 0;
  position: relative;
  top: 0;
  width: 1205px;
}

